import { useState, useContext } from 'react'
import { useSkin } from '@hooks/useSkin'
import { Link, useHistory } from 'react-router-dom'
import { Facebook, Twitter, Mail, GitHub } from 'react-feather'
import InputPasswordToggle from '@components/input-password-toggle'
import { Row, Col, CardTitle, Spinner, Form, FormGroup, Label, Input, CustomInput, Button } from 'reactstrap'
import '@styles/base/pages/page-auth.scss'
import { PHONE_PREFIX } from '../../Configuarables'
import InputGroup from 'reactstrap/lib/InputGroup'
import InputGroupAddon from 'reactstrap/lib/InputGroupAddon'
import InputGroupText from 'reactstrap/lib/InputGroupText'
import useJwt from '@src/auth/jwt/useJwt'
import { Error, Success, ErrorMessage } from '../../viewhelper'
import { toast } from 'react-toastify'
import { handleLogin } from '@store/actions/auth'
import { useDispatch } from 'react-redux'
import { AbilityContext } from '@src/utility/context/Can'
import InfinitiProperty from '../../../assets/images/logo/InfinitiProperty.png'

const Login = () => {
  const [skin, setSkin] = useSkin()
  const illustration = skin === 'dark' ? 'login-v2-dark.svg' : 'login-v2.svg',
    source = require(`@src/assets/images/pages/${illustration}`).default
  const ability = useContext(AbilityContext)
  const dispatch = useDispatch()
  const history = useHistory()

  const [isLoading, setLoading] = useState(false)
  const [ShowPwd, setShowPwd] = useState(null)
  // const [isRegistered, setIsRegistered] = useState(null)

  const [mobile, setMobile] = useState(null)
  const [password, setPassword] = useState(null)
  const [newPassword, setNewPassword] = useState(null)
  const [confirmNewPassword, setConfirmNewPassword] = useState(null)

  const handleMobileChange = e => {
    setShowPwd(null)
    if (/^[0-9]*$/.test(e.target.value)) {
      setMobile(e.target.value)
    }
  }
  const handleLoginSuccess = resData => {
    const { token, user_info, usermenu } = resData
    const role = String(resData.user_info.user_type_name).replace(/\s/g, '')
    const data = {
      ...user_info,
      accessToken: token,
      refreshToken: token,
      extras: {
        eCommerceCartItemsCount: 5
      },
      role,
      ability: [
        {
          action: 'manage',
          subject: role
        },
        {
          action: 'manage',
          subject: 'Auth'
        }
      ]
    }
    dispatch(handleLogin(data))
    ability.update(data.ability)
    history.push(`/${role}/Dashboard`)
    // if (role === 'EstateManager') {
    //   setTimeout(() => {
    //     alert('api calling')
    //   }, 2000)
    // }
    // toast.success(
    //   <ToastContent name={data.fullname || data.Mobile || 'Anonymous'} role={data.role || 'Anonymous'} />,
    //   { transition: Slide, hideProgressBar: true, autoClose: 2000 }
    // )
  }
  const handleSubmit = e => {
    e.preventDefault()
    setLoading(true)
    if (!ShowPwd) {
      ShowPwd === null ? useJwt.checkMobile({ mobile }).then(res => {
        setLoading(false)
        setShowPwd(res.data.payload.is_registered)
        // setIsRegistered(res.data.payload.is_registered)
        // if (res.data.payload.is_registered) {
        // } else {
        // }
      }).catch(err => {
        Error(err)
        console.log(err)
        setLoading(false)
      }) : confirmNewPassword === newPassword ? useJwt.newPwdSet({ mobile, password: newPassword }).then(res => {
        setLoading(false)
        handleLoginSuccess(res.data.payload)
        console.log('IPlogin', res.data.payload)
        console.log('userRole', String(res.data.payload.user_info.user_type_name).replace(/\s/g, ''))
      }).catch(err => {
        Error(err)
        console.log('IPlogin', err)
        setLoading(false)
      }) : (function () {
        setLoading(false)
        toast.error('Confirm password not matched!')
      })()
    } else {
      useJwt.IPlogin({ mobile, password }).then(res => {
        setLoading(false)
        console.log('IPlogin', res.data.payload)
        console.log('userRole', String(res.data.payload.user_info.user_type_name).replace(/\s/g, ''))
        handleLoginSuccess(res.data.payload)
      }).catch(err => {
        Error(err)
        console.log('IPlogin', err)
        setLoading(false)
      })
    }
  }

  return (
    <div className='auth-wrapper auth-v2'>
      <Row className='auth-inner m-0'>
        <Col className='d-none d-lg-flex align-items-center p-5' lg='8' sm='12' style={{ background: '#016496' }}>
          <div className='w-100 d-lg-flex align-items-center justify-content-center px-5'>
            <img className='img-fluid' src={InfinitiProperty} alt='Login V2' />
          </div>
        </Col>
        <Col className='d-flex align-items-center auth-bg px-2 p-lg-5' lg='4' sm='12'>
          <Col className='px-xl-2 mx-auto' sm='8' md='6' lg='12'>
            <CardTitle tag='h2' className='font-weight-bold mb-1 text-center'>
              Welcome <br />
              <small>Please Sign in to your account</small>
            </CardTitle>
            {/* <div className="text-center">
              <img src={InfinitiProperty} alt="logo" style={{ borderRadius: '10px', width: '150px' }} />
            </div> */}
            <Form className='auth-login-form mt-2' onSubmit={handleSubmit}>
              <FormGroup>
                <Label className='form-label' for='Mobile'>
                  Mobile Number
                </Label>
                <InputGroup className='input-group-merge w-100 mb-1'>
                  <InputGroupAddon addonType='prepend'>
                    <InputGroupText>
                      {PHONE_PREFIX}
                    </InputGroupText>
                  </InputGroupAddon>
                  <Input
                    autoFocus
                    required
                    type='text'
                    minLength={10}
                    maxLength={10}
                    id='Mobile'
                    name='mobile'
                    value={mobile || ''}
                    onChange={handleMobileChange}
                    placeholder='123-456-7899'
                  />
                </InputGroup>
              </FormGroup>
              {ShowPwd ? <FormGroup>
                <div className='d-flex justify-content-between'>
                  <Label className='form-label' for='login-password'>
                    Password
                  </Label>
                  <Link to='#'>
                    <small>Forgot Password?</small>
                  </Link>
                </div>
                <InputPasswordToggle
                  required
                  className='input-group-merge'
                  minLength={6}
                  id='login-password'
                  name='password'
                  value={password || ''}
                  onChange={e => setPassword(e.target.value)}
                />
              </FormGroup> : ShowPwd === false && <>
                <FormGroup>
                  <Label className='form-label' for='newPassword'>
                    Set New Password
                  </Label>
                  <InputPasswordToggle
                    required
                    className='input-group-merge'
                    minLength={6}
                    id='newPassword'
                    name='newPassword'
                    value={newPassword || ''}
                    onChange={e => setNewPassword(e.target.value)}
                  />
                </FormGroup>
                <FormGroup>
                  <Label className='form-label' for='confirmNewPassword'>
                    Confirm  Password
                  </Label>
                  <InputPasswordToggle
                    required
                    className='input-group-merge'
                    minLength={6}
                    id='confirmNewPassword'
                    name='confirmNewPassword'
                    value={confirmNewPassword || ''}
                    onChange={e => setConfirmNewPassword(e.target.value)}
                  />
                </FormGroup></>}
              <Button.Ripple color='primary' block type='submit' disabled={isLoading} >
                {isLoading ? <><Spinner color='white' size='sm' />&nbsp; Loading </> : 'Sign in'}
              </Button.Ripple>
            </Form>
          </Col>
        </Col>
      </Row>
    </div>
  )
}

export default Login